import React from "react";
import { graphql } from "gatsby";
import About from "../components/About";
import Experience from "../components/Experience";
import { ExperienceData } from "../data/experience";
import Landing from "../components/Landing";
import Projects from "../components/Projects";
import { ProjectsData } from "../data/projects";

const Index = ({ data }) => {
  console.log("data", data);
  return (
    <div>
      <Landing desktop={"ss"} mobile={"ss"} title="Web Developer" data={data} />
      <About />
      <Experience data={ExperienceData} />
      <Projects
        title="Projects"
        allProjectImages={data.projectPreviews.edges}
        projects={ProjectsData}
      />
    </div>
  );
};

export default Index;

export const query = graphql`
  query {
    desktopImage: file(relativePath: { eq: "manoj-big.png" }) {
      childImageSharp {
        fluid(maxWidth: 800) {
          ...GatsbyImageSharpFluid_noBase64
        }
      }
    }
    mobileImage: file(relativePath: { eq: "manoj.png" }) {
      childImageSharp {
        fluid(maxWidth: 500) {
          ...GatsbyImageSharpFluid_noBase64
        }
      }
    }
    projectPreviews: allFile(
      filter: { relativeDirectory: { eq: "projects" } }
    ) {
      edges {
        node {
          id
          childImageSharp {
            fluid(maxWidth: 600) {
              originalName
              ...GatsbyImageSharpFluid_noBase64
            }
          }
        }
      }
    }
  }
`;
